import { Component } from '@angular/core';

@Component({
  selector: 'jih-colors',
  templateUrl: './color.component.html'
})
export class ColorComponent {
  red: number = 255;
  green: number = 255;
  blue: number = 255;
  hex = (num: number): string => num.toString(16).length === 1 ? '0' + num.toString(16) : num.toString(16);
  get bgColor() {
    return '#' + this.hex(this.red) + this.hex(this.green) + this.hex(this.blue);
  }

  onChangeRed = (value: string): number => {
    const newValue: number = parseInt(value, 16);
    if (isNaN(newValue) || value.length < 2) {
      return this.red;
    }
    return this.red = newValue;
  }

  onChangeGreen = (value: string): number => {
    const newValue: number = parseInt(value, 16);
    if (isNaN(newValue) || value.length < 2) {
      return this.green;
    }
    return this.green = newValue;
  }

  onChangeBlue = (value: string): number => {
    const newValue: number = parseInt(value, 16);
    if (isNaN(newValue) || value.length < 2) {
      return this.blue;
    }
    return this.blue = newValue;
  }


}
